import Global from "../components/Global";
import "../common/extensions/jquery-et";

import { DefaultStatus, getStatusText } from "../common/DefaultStatus";
import tinyMceLoader from "../../../../common/TinyMceHelper";
import "../components/coverImgOper";

import "../../scss/pages/essays-edit.scss";
import DtPicker from "../components/dateTimePickerHelper";
import PinYin from "../common/PinYin";

$(() => {
    const essayApi = "/api/Essays/Edit";
    const essayTypesApi = "/api/Taxonomy/EssayTypes";
    const tagsApi = "/api/Taxonomy/Tags";
    const postCallBackUrl = "/BackCms/Essays/Index";

    let createDtPicker: DtPicker;
    let modifyDtPicker: DtPicker;


    //用于判断文章分类与标签的select是否加载完毕
    let essayTypeReady = false;
    let tagsTypeReady = false;
    /**
     * 获取所有文件类型与标签
     */
    const getTaxonomy = () => {
        $.ajax({
            url: essayTypesApi,
            type: "get",
            success: (msg: any) => {
                if (msg.Code === 0) {
                    let data = msg.Data;
                    let optionsHtml = data.map((item: any) => {
                        return `<option value="${item.Name}">${item.Name}</option>`
                    }).join(" ");
                    $("#essayTypes").html(optionsHtml).selectpicker('refresh');
                }
            },
            complete: () => {
                essayTypeReady = true;
            }
        });
        $.ajax({
            url: tagsApi,
            type: "get",
            success: (msg: any) => {
                if (msg.Code === 0) {
                    let data = msg.Data;
                    let optionsHtml = data.map((item: any) => {
                        return `<option value="${item.Name}">${item.Name}</option>`
                    }).join(" ");
                    $("#tags").html(optionsHtml).selectpicker('refresh');
                }
            },
            complete: () => {
                tagsTypeReady = true;
            }
        });
    }
    getTaxonomy();

    /**
     * 编辑器切换事件绑定
     */
    $("#editorSwitcher").click((e) => {
        e.preventDefault();
        var type = $("#editorSwitcher").data('type');
        if (type === 'tiny') {
            $("#editorSwitcher").data('type', 'md');
            $("#tinyRow").css("display", 'none');
            $("#mdRow").css("display", 'block');
            $("#editorSwitcher").html("TinyMCE编辑器");
        } else {
            $("#editorSwitcher").data('type', 'tiny');
            $("#tinyRow").css("display", 'block');
            $("#mdRow").css("display", 'none');
            $("#editorSwitcher").html("Markdown编辑器");

            var mdPreview = document.getElementById('htmlPreview').innerHTML;
            //console.log("mdPreview", mdPreview);
            if (mdPreview) {
                tinyMceLoader.setContent(mdPreview, "tinyEditor");
            }
        }
    });

    /**
     * title --> slug 事件绑定
     */
    $("#title").on("blur", (e: any) => {
        let titleVal = <string>$("#title").val();
        let pinYin = new PinYin({ charCase: 0 });
        let originPinyin = pinYin.getFullChars(titleVal);
        titleVal = originPinyin
            .replace("C#", "CSharp")
            .replace("c#", "CSharp")
            .replace(" ", "-");
        $("#slug").val(encodeURIComponent(titleVal));
    });

    $(".addTaxonomy").click((e) => {
        e.preventDefault();
        var type = $(e.target).data("type");
        if (type === 'essayType') {
            //console.log('essayType');
            Global.openDialog("添加分类",
                "/BackCms/Taxonomy/AddEssayType",
                (index) => {
                    $("#layui-layer-iframe" + index).css({
                        "padding": '1em',
                    });
                },
                () => {
                    getTaxonomy();
                }, () => {
                    // do nothing
                });
        } else if (type === 'tag') {
            Global.openDialog("添加标签",
                "/BackCms/Taxonomy/AddTag",
                (index) => {
                    $("#layui-layer-iframe" + index).css({
                        "padding": '1em',
                    });
                },
                () => {
                    getTaxonomy();
                }, () => {
                    // ignore
                });
        }
    });

    /**
     * 初始化DtPicker
     */
    const initDtPicker = () => {
        createDtPicker = new DtPicker("#createDateTimePicker");
        modifyDtPicker = new DtPicker("#modifyDateTimePicker");

        createDtPicker.on("dp.change", (e: any) => {
            modifyDtPicker.setMinDt(createDtPicker.getDt());
        });

        modifyDtPicker.on("dp.change", (e: any) => {
            createDtPicker.setMaxDt(modifyDtPicker.getDt());
        });

        $("#curTime").click(() => {
            modifyDtPicker.setDt(new Date());
        });
    }
    initDtPicker();

    let editInfo = Global.getEditInfo();
    /**
     * 初始化ui
     * @param data essay实体
     */
    const initUi = (data: any) => {
        if (essayTypeReady && tagsTypeReady) {
            $("#title").val(data.Title).data("id", data.Id);
            $("#slug").val(data.Slug);

            $("#coverImg").val(data.CoverImg);
            $('#essayTypes').val(data.EssayType)
                .selectpicker('render');
            // $("#essayTypes").selectpicker("val", data.EssayType);
            let tags = data.Tags.split("|");
            $("#tags").selectpicker("val", tags);

            $("#introduction").val(data.Introduce);
            createDtPicker.setDt(data.Create);
            // modifyDtPicker.setDt(data.Modify);
            $("#readNum").val(data.ReadNum);
            $("#praise").val(data.Praise);
            $("#criticize").val(data.Criticize);
            console.log(data.Sticky)
            if (data.Sticky) {
                $("#sticky").attr('checked', 'checked')
            }
            if (data.Status === DefaultStatus.Secret) {
                $("#secret").attr('checked', 'checked')
            }
            tinyMceLoader.setContent(data.Content, "tinyEditor");
        } else {
            window.setTimeout(() => {
                initUi(data);
            }, 1000);
        }
    }

    /**
     * 请求文章
     * @param essayId 请求文章的id
     */
    const queryEssayData = (essayId: any) => {
        $.ajax({
            url: essayApi,
            data: {
                id: essayId
            },
            type: "get",
            success: (msg: any) => {
                if (msg.Code === 0) {
                    let data = msg.Data;
                    initUi(data);
                }
            }
        });
    }

    tinyMceLoader.init("#tinyEditor", 'full', 440, "100%", {
        verify_html: false
    });

    //判断是否为编辑模式
    if (editInfo.isEdit) {
        queryEssayData(editInfo.id);
    }

    /**
     * 获取文章的目录
     * @param {any} $content 文章内容
     * @return {Array} 文章目录数组
     */
    const getCatalog = ($content: any) => {
        var tocArr:any = [];
        $("h1,h2,h3,h4,h5,h6", $content).each(function (index, item) {
            var tagName = $(item).get(0).localName;
            var id = "hl" + index;
            $(item).attr("id", id);
            var text = $(this).text();
            tocArr.push('<a href="#' + id + '" class="toc' + tagName + '" >' + text + '</a>');
        });
        return tocArr;
    };

    //submit事件绑定
    const handlerSubmit = () => {

        const submit = (data: any) => {
            var type = $("#editorSwitcher").data('type');
            if (type !== 'tiny') {
                var mdPreview = document.getElementById('htmlPreview').innerHTML;
                if (mdPreview) {
                    tinyMceLoader.setContent(mdPreview, "tinyEditor");
                }
            } 

            let content = tinyMceLoader.getContent("tinyEditor");
            data.tags = (<any>$("#tags").val()).join("|");
            let $content = $("<div>" + content + "</div>");
            $content.find("img").addClass("img-responsive");
            let arr = getCatalog($content);
            if (arr.length === 0) {
                data.Catalog = "none";
            } else {
                data.Catalog = arr.join("");
            }
            data.content = $content.html();
            if (data.sticky) {
                data.sticky = true;
            } else {
                data.sticky = false;
            }
            data.id = $("#title").data("id");
            data.slug = $("#slug").val();
            Global.showLoading();
            $.ajax({
                url: essayApi,
                type: "post",
                data: data,
                success: (msg: any) => {
                    if (msg.Code === 0 && msg.Data) {
                        if (editInfo.isEdit) {
                            Global.alert("修改成功");
                        } else {
                            Global.alert("添加成功");
                        }
                        if (postCallBackUrl) {
                            window.setTimeout(() => {
                                    location.href = postCallBackUrl;
                                },
                                1000);
                        }

                    } else {
                        Global.alertError(msg.Error);
                    }
                },
                complete: () => {
                    Global.hideLoading();
                }
            });
        }

        $("#publish").click((e: any) => {
            e.preventDefault();
            let forDataObj = Global.getFromDataObj("#editForm");
            if ($("#secret").is(":checked")) {
                forDataObj.status = DefaultStatus.Secret;
            } else {
                forDataObj.status = DefaultStatus.Published;
            }
            submit(forDataObj);
        });

        $("#draft").click((e: any) => {
            e.preventDefault();
            let forDataObj = Global.getFromDataObj("#editForm");
            if ($("#secret").is(":checked")) {
                forDataObj.status = DefaultStatus.Secret;
            } else {
                forDataObj.status = DefaultStatus.Draft;
            }
            submit(forDataObj);
        });
    }
    handlerSubmit();
});