import "../../scss/components/coverImg.scss";
(()=>{
    const coverImgListApi = "/api/Essays/CoverImgList";
    const uploadUrl = "/api/Essays/CoverImgUpload";
    ////封面图片相关
    $('#myModal').modal({
        backdrop: "static",
        show: false
    });
    
    const onImgClick = (e: any) => {
        $(".selectImg").removeClass("selectImg");
        $(e.target).addClass("selectImg");
    }
    /**
     * 获取图片列表
     */
    const getList = function () {
        $.ajax({
            url: coverImgListApi,
            type: "get", //使用get方法访问后台
            async: true, //true:异步请求
            complete: function () {
                $(".fa-refresh").removeClass("fa-spin");
            },
            success: function (msg: any) {
                if (msg.Code === 0) {
                    let imgs = msg.Data;
                    let liHtml = imgs.map((item: any) => {
                        if (!item.startsWith("/")) {
                            item = "/" + item;
                        }
                        return "<li><img src=" + item + " style='width:200px;height:145px;' /></li>";
                    }).join(" ");
    
                    $("#imgList").html(liHtml);
                    $("#imgList li").click((e: any) => {
                        onImgClick(e);
                    });
                } else {
                    $("#imgList").html(msg.exception);
                }
            }
        });
    }
    
    /**
     * 打开列表
     */
    $("#coverImgList").click(function (e: any) {
        e.preventDefault();
    
        if ($("#imgList li").length === 0) {
            $(".fa-refresh").addClass("fa-spin");
            setTimeout(function () {
                getList();
            }, 1000);
        }
        $('#myModal').modal('show');
    
    });
    
    /**
     * 刷新
     */
    $(".fa-refresh").click(function () {
        $(".fa-refresh").addClass("fa-spin");
        setTimeout(function () {
            getList();
        }, 1000);
    });
    
    /**
     * 显示上传封面图片panel
     */
    $(".fa-cloud-upload").click(function () {
        if ($("#imgList").css("display") === "none") {
            $("#imgUp").hide();
            $("#imgList").show();
        } else {
            $("#imgList").hide();
            $("#imgUp").show();
        }
    
    });
    
    /**
     * 确定封面图片
     */
    $("#selectImg").click(function () {
        let $selectImg = $(".selectImg");
        if ($selectImg.length === 0) {
            alert("尚未选择！");
        } else {
            var src = $selectImg.attr("src"); 
            $("#coverImg").val(src);
            $('#myModal').modal('hide');
        }
    
    });
    
    /**
     * fileinput事件绑定
     */
    $("#fileUpload").fileinput({
        language: "zh",
        theme: 'fa',
        uploadUrl: uploadUrl,
        allowedFileExtensions: ['jpg', 'gif', 'png']//接收的文件后缀
    }).on("fileuploaded",
        function (event: any, data: any, previewId: any, index: any) {
            // console.log(event, data, previewId, index)
            var msg = data.response;
            if (msg.Code !== 0) {
                alert("上传出错！");
                console.error(msg.Error);
            } else {
                let data = msg.Data;
                if (data.StateCode === 2) {
                    let $newImg = $("<li><img src=" + data.Url + " style='width:200px;height:145px;' /></li>");
                    $newImg.click((e: any) => {
                        onImgClick(e);
                    });
                    $("#imgList").prepend($newImg);
                }
                else if (data.StateCode === 4) {
                    alert("文件已存在");
                }
                else {
                    alert(data.ErrorMessage);
                }
            }
            $("#imgUp").hide();
            $("#imgList").show();
        });
})();